import {ADD_ENTRY, ADD_ENTRIES, CLEAR_ENTRIES} from "../constants/ActionTypes";

const initialState = [];

function addEntry(state, entry) {
    let found = state.find(user => user.id === entry.id);

    return found ? state : [...state, entry];
}

export default function users(state = initialState, action) {
    switch (action.type) {
        case ADD_ENTRY:
            return addEntry(state, action.entry);
        case ADD_ENTRIES: {
            let newState = [...state];
            for (let i = 0; i < action.entries.length; i++) {
                newState = addEntry(newState, action.entries[i]);
            }
            return newState;
        }
        case CLEAR_ENTRIES: {
            return initialState;
        }
        default:
            return state;
    }
}



